import { Helmet } from "react-helmet";
import { Link, useNavigate, useLocation } from "react-router-dom";
import { useEffect, useState } from "react";
import { useFormik } from "formik";
import { toast } from "react-toastify";
import axios from "axios";
import { ToastContainer } from "react-toastify";

import "react-toastify/dist/ReactToastify.css";
// import "../styles/Login.css";
import "../GlobalCSS.css";
import styled from "styled-components";

function Login() {
  const location = useLocation();
  const [disabledSubmit, setDisabledSubmit] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [state, updateState] = useState({ name: "", email: "", social_id: "" });
  const navigate = useNavigate();
  useEffect(() => {
    if (location.state) {
      toast("Password reset successfully", {
        position: "top-right",
        autoClose: 2000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        type: "success",
      });
      window.history.replaceState({}, document.title);
    }
    // console.log('the state after update is ', state)
  }, [state, location]);

  const validate = (values) => {
    const errors = {};

    if (!values.email) {
      errors.email = "Email is required";
    } else if (
      !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
    ) {
      errors.email = "Invalid email address";
    }
    if (!values.password) {
      errors.password = "Password is required";
    }

    return errors;
  };

  const showhidePassword = () => {
    setShowPassword(!showPassword);
  };

  const formik = useFormik({
    initialValues: {
      email: "",
      password: "",
    },
    validate,
    onSubmit: async (values) => {
      setDisabledSubmit(true);
      try {
        let res = await axios.post(`auth/login`, values);
        // console.log("resssss------",res.data.data)
        localStorage.setItem(
          "angolosport_auth_back",
          JSON.stringify(res.data.data)
        );
        navigate("/dashboard");
      } catch (errors) {
        toast(errors.response?.data?.message, {
          position: "top-right",
          autoClose: 2000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          type: "error",
        });
      }
      setDisabledSubmit(false);
    },
  });

  return (
    <Container>
      <ToastContainer
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
      <Helmet>
        <title>Login</title>
      </Helmet>
      <main className="main-content  mt-0">
        <div
          style={
            {
              // backgroundColor: "#859bc7",
              // backgroundImage: `url("https://t3.ftcdn.net/jpg/02/78/42/76/360_F_278427683_zeS9ihPAO61QhHqdU1fOaPk2UClfgPcW.jpg)`,
            }
          }
          className="page-header align-items-start min-vh-100 bgColor"
        >
          <span className="mask  opacity-6"></span>
          <div className="container my-auto">
            <div className="row">
              <div className="col-lg-6 col-md-8 col-12 mx-auto">
                <div className="card z-index-0 fadeIn3 fadeInBottom">
                  <div className="card-header p-0 position-relative mt-n4 mx-3 z-index-2">
                    <div
                      style={{
                        // backgroundColor: "#00346C",
                        backgroundColor: "orange",
                      }}
                      className="shadow-primary border-radius-lg py-3 pe-1"
                    >
                      <h4 className="text-white font-weight-bolder text-center mt-2 mb-0">
                        Login In
                      </h4>
                    </div>
                  </div>
                  <div className="card-body">
                    <form onSubmit={formik.handleSubmit}>
                      <div className="input-group input-group-outline my-3">
                        <input
                          type="email"
                          id="email"
                          name="email"
                          placeholder="Email"
                          value={formik.values.email}
                          onChange={formik.handleChange}
                          className="form-control fs-6"
                        />
                      </div>
                      {formik.errors.email ? (
                        <div className="text-danger">{formik.errors.email}</div>
                      ) : null}

                      <div
                        className="input-group input-group-outline mb-3 pas-eye"
                        style={{
                          border: "1px solid #d6d4d0",
                          borderRadius: "4px",
                        }}
                      >
                        <input
                          type={showPassword ? "text" : "password"}
                          id="password"
                          name="password"
                          placeholder="Password"
                          value={formik.values.password}
                          onChange={formik.handleChange}
                          className="form-control fs-6 border-0"
                        />
                        <i
                          className={
                            showPassword
                              ? "fas fa-eye"
                              : "fas fa-eye-slash mt-2 pe-2"
                          }
                          onClick={() => showhidePassword()}
                        ></i>
                      </div>
                      {formik.errors.password ? (
                        <div className="text-danger">
                          {formik.errors.password}
                        </div>
                      ) : null}

                      <div className="text-center">
                        <button
                          type="submit"
                          style={{
                            // backgroundColor: "#00346C",
                            backgroundColor: "orange",
                            color: "#fff",
                          }}
                          className="btn  w-100 my-4 mb-2"
                          disabled={disabledSubmit}
                        >
                          {disabledSubmit ? (
                            <div>
                              <span
                                className="spinner-border spinner-border-sm"
                                role="status"
                                aria-hidden="true"
                              ></span>
                              <span className="sr-only"></span> Logging In
                            </div>
                          ) : (
                            <span className="fs-6">Login In</span>
                          )}
                        </button>
                      </div>
                      <p className="mt-4 text-sm text-center">
                        <Link
                          to={"forgot-password"}
                          className="text-primary fs-6 text-gradient font-weight-bold"
                        >
                          Forgot Password?
                        </Link>
                      </p>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <footer className="footer position-absolute bottom-2 py-2 w-100">
            <div className="container">
              <div className="row align-items-center justify-content-lg-between">
                <div className="col-12 col-md-6 my-auto">
                  <div className="copyright text-center text-sm text-white text-lg-start">
                    Â© Algo_Sport
                  </div>
                </div>
              </div>
            </div>
          </footer>
        </div>
      </main>
    </Container>
  );
}

export default Login;

